* {
  /* box-sizing: border-box; */
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  /* background: var(--color-bg); */
}

a {
  color: unset;
  text-decoration: none;
}

.circle_top_login {
  z-index: -1;
  position: absolute;
  /* width: 82vh;
  height: 82vh; */
  width: 28rem;
  height: 28rem;
  border-radius: 100%;
  background: rgba(49, 177, 39, 0.63);
  animation: roll-in-down 1000ms ease-out;
  top: -29%;
  left: -22vh;
}

.circle_bottom_login {
  position: absolute;
  bottom: -29%; 
  right: -22vh;
  z-index: -1;
  bottom: -29%;
  /* width: 82vh;
  height: 82vh; */

  width: 28em;
  height: 28em;
  border-radius: 100%;
  background: #24A2E9;
  animation: roll-in-top 1000ms ease-out;
}

.circle_bottom_signup {
  position: absolute;
  width: 82vh;
  height: 82vh;
  border-radius: 100%;
  background: #31B127A1;

  animation: roll-in-top 1000ms ease-out;
  z-index: -1;
  bottom: -29%;
  left: -22vh;
}

.circle_top_signup {
  z-index: -1;
  position: absolute;
  width: 82vh;
  height: 82vh;
  right: -22vh;
  top: -29%;
  border-radius: 100%;
  background: #24A2E9;
  animation: roll-in-down 1000ms ease-out;
}





@keyframes roll-in-top {

  0% {
    transform: translateY(-472px) rotate(-540deg);
    opacity: 0;
  }

  100% {

    transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@keyframes roll-in-down {

  0% {
    transform: translateY(472px) rotate(540deg);
    opacity: 0;
  }

  100% {

    transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

.login_signup_container{
  z-index: -1;
  width: 100vw;
  height: 100vh;
  
  position: absolute;
  background: pink;
  overflow-y: hidden;
  overflow-x: hidden;
  /* overflow-y: overlay; */
  
}
.login_signup_glass {
  position: absolute;
  left: 3%;
  top: 5.7%;
  bottom: 5.7%;
  right: 3%;
  background: rgba(236, 236, 236, 0.34);
  backdrop-filter: blur(10px);
  border-radius: 30px;
}