.navbar {
    float: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 4.6rem;
    margin-left: 7rem;
}

.logo {
    margin-right: 2rem;
}

.logo img {
    width: 29px;
    height: 46px;
}

.nav-list {
    display: flex;
    flex: 1;
    justify-content: flex-start;
    align-items: center;
}

.nav-list-home {
    display: flex;
    flex: 1;
    justify-content: flex-start;
    align-items: center;
    color: #fff;
}

.nav-list li,
.nav-list-home li,
.navbar_sign {
    list-style: none;
    display: flex;
    flex-direction: row;
    font-family: var(--font-family-Comfortaa);
    font-weight: 500;
    font-size: 15px;
    line-height: 16.73px;
    text-transform: capitalize;
    padding-left: 3rem;
    cursor: pointer;
}

.nav-list li a {
    text-decoration: none;
    color: #19177E;
}

.nav-list li a:hover,
.nav-list-home li a:hover {
    text-decoration: none;
    color: brown;
}

.navbar_sign {
    /* box-sizing: border-box; */
    position: absolute;
    right: 10%;
    top: 13%;
    width: 15vh;
    height: 6vh;
    border: none;
    border: 2px solid;
    border-image-source: linear-gradient(89.95deg, rgba(50, 177, 38, 0.8) 1.99%, rgba(36, 162, 233, 0.8) 85.34%, rgba(36, 162, 233, 0.8) 104.58%);
    border-image-slice: 1%;
}
.navbar_sign button:hover,
.navbar_home_btn:hover{
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
}

.navbar_home_btn {
    background: #0C0B38;
    /* box-sizing: border-box; */
    position: absolute;
    right: 10%;
    top: 13%;

    width: 18vh;
    height: 7vh;
    border: none;
    border: 2px solid;

    border-image-source: linear-gradient(89.95deg, rgba(50, 177, 38, 0.8) 1.99%, rgba(36, 162, 233, 0.8) 85.34%, rgba(36, 162, 233, 0.8) 104.58%);
    border-image-slice: 1%;

}